/* The Modal (background) */
.modal {
	display: none; /* Hidden by default */
	position: fixed; /* Stay in place */
	z-index: 9999; /* Sit on top */
	padding: 40px 0; /* Location of the box */ 
	left: 0;
	top: 0;
	width: 100%; /* Full width */
	height: 100%; /* Full height */
	overflow: auto; /* Enable scroll if needed */
	background-color: rgb(0,0,0); /* Fallback color */
	background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

/* Modal Content */
.modal-content {
	position: relative;
	background-color: #fefefe;
	margin: auto;
	padding: 0;
	border: 1px solid #888;
	width: 90%;
	max-width: 600px;
	box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);
	-webkit-animation-name: animatetop;
	-webkit-animation-duration: 0.4s;
	animation-name: animatetop;
	animation-duration: 0.4s
}


@keyframes animatetop {
	from {top:-300px; opacity:0}
	to {top:0; opacity:1}
}

/* The Close Button */
.close {
	color: white;
	float: right;
	font-size: 28px;
	font-weight: bold;
}

.close:hover,
.close:focus {
	color: red;
	text-decoration: none;
	cursor: pointer;
}

.modal-header {
	padding: 2px 16px;
	background: linear-gradient(to right, rgba(224,108,160,1) 0%, rgba(236,79,100,1) 100%);
	color: white;
}

.modal-body {padding: 15px 15px;}

.modd{
	&__wrr{
		padding: 60px;
		position: relative;
		// text-align: center;
	}
	&__title{
		font-size: 38px;
		text-align: center;
		margin-bottom: 20px;
	}
	&-1{
		.modal-content{
			max-width: 500px;
		}
	}
	&-2{
		.modal-content{
			max-width: 800px;
		}
		&__fields{
			display: flex;
			flex-wrap: wrap;
			font-size: 14px;
			justify-content: space-between;
			span{
				display: inline-block;
				margin-right: 30px;
			}
			.jum__calc-inp{
				display: inline-block;
			}
			p{
				margin-bottom:10px
			}
		}
	}
	&__submit{
		margin-top: 40px;
		margin-left: -20px;
	}
	&__close{
		position: absolute;
		right: 20px;
		top: 10px;
		color: red;
		cursor: pointer;
		font-size: 30px;
	}
	&__descr{
		text-align: center;
		font-size: 24px;
		margin-bottom: 20px;
	}
}